/** Media Queries **/
.acciones {
  margin-top: 5rem;
  text-align: center;
}
.acciones p {
  margin: 0;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  align-self: center;
  margin-bottom: 2rem;
  color: #000;
  font-size: 1.8rem;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}